import { Button, Grid, Link } from "@mui/material";
import React from "react";
import { makeStyles } from "@mui/styles";

import footerImage from "../assets/footer_image.png";
import "./Footer.css";

const useStyles = makeStyles((theme) => ({
  footerLeft: {
    padding: "200px 0",
    [theme.breakpoints.down("md")]: {
      padding: "100px 0",
    },
  },
  footerRight: {
    padding: "200px 0",
    backgroundColor: "#FDBA66",
    [theme.breakpoints.down("md")]: {
      padding: "100px 0",
    },
  },
  footerLeftContent: {
    position: "relative",
    left: "50px",
    justifyContent: "flex-end",
    [theme.breakpoints.down("md")]: {
      justifyContent: "center",
      left: "0",
    },
  },
}));

const iconLink = {
  facebook: "https://bit.ly/3L5UC9s",
  linkedIn: "https://bit.ly/3D5iuY2",
  instagram: "http://instagram.com/rajeshganeshsharmaa",
  youtube: "https://www.youtube.com/channel/UCAiWW0hs-PxqMZScGaY5piw",
  imdb: "https://imdb.to/3JF1IkZ",
  twitter: "https://bit.ly/3JCiZvl",
};

function Footer() {
  const classes = useStyles();
  return (
    <div id="contact">
      <Grid container>
        <Grid item container xs={12} md={6} className={classes.footerLeft}>
          <Grid
            item
            container
            // justifyContent="flex-end"
            className={classes.footerLeftContent}
          >
            <Grid item>
              <Grid
                container
                justifyContent="flex-end"
                alignItems="flex-end"
                sx={{ height: "100%" }}
              >
                <Grid
                  item
                  container
                  direction="column"
                  sx={{ backgroundColor: "#0D2427", padding: "10px 0" }}
                >
                  <Link href={iconLink.facebook}>
                    <i className="fa-brands fa-facebook-f footer_icon "></i>
                  </Link>
                  <Link href={iconLink.linkedIn}>
                    <i className="fa-brands fa-linkedin-in footer_icon"></i>
                  </Link>
                  <Link href={iconLink.instagram}>
                    <i className="fa-brands fa-instagram footer_icon"></i>
                  </Link>
                  <Link href={iconLink.youtube}>
                    <i className="fa-brands fa-youtube footer_icon"></i>
                  </Link>
                </Grid>
              </Grid>
            </Grid>
            <Grid item sx={{ height: "100%" }}>
              <Grid
                container
                direction="row"
                justifyContent="flex-start"
                alignItems="flex-end"
              >
                <img src={footerImage} alt="" className="footer_image" />
              </Grid>
            </Grid>
            {/* here */}
          </Grid>
        </Grid>
        <Grid
          item
          container
          xs={12}
          md={6}
          className={classes.footerRight}
          justifyContent="center"
          alignItems="center"
        >
          <div className="footer_content">
            <p>Contact Me</p>
            <h2>Get In Touch</h2>
            <Grid container>
              <Grid item container>
                <Grid item>
                  <h3 className="footer_right_h3">Contact No:</h3>
                </Grid>
                <Grid item>
                  <p className="footer_right_text">+91-9820476993</p>
                </Grid>
              </Grid>
              <Grid item container sx={{ paddingTop: "25px" }}>
                <Grid item>
                  <h3 className="footer_right_h3">Email Id:</h3>
                </Grid>
                <Grid item>
                  <p className="footer_right_text">
                    Rajeshsharma9284@gmail.com
                  </p>
                </Grid>
              </Grid>
            </Grid>
            {/* <Button
              variant="contained"
              size="large"
              style={{
                backgroundColor: "#0D2427",
                color: "white",
                fontSize: "16px",
                padding: "10px",
              }}
            >
              Contact Me
            </Button> */}
          </div>
        </Grid>
      </Grid>
      <div className="footer_bottom">
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          alignItems="center"
          sx={{ minHeight: "235px", backgroundColor: "#0D2427" }}
        >
          <Grid item>
            <div className="footer_bottom_left">
              <p>Actor in Mumbai</p>
              <h3>RAJESH GANESH SHARMA</h3>
              <p>Actor</p>
            </div>
          </Grid>
          <Grid item>
            <div className="footer_bottom_right">
              <p>Designed and Developed By</p>
              <h3>OrbTech Agency</h3>
            </div>
            {/* <div className="footer_bottom_right">
              <Button
                variant="contained"
                size="large"
                style={{
                  backgroundColor: "#F56845",
                  color: "white",
                  fontSize: "16px",
                }}
              >
                Resume
              </Button>
            </div> */}
          </Grid>
        </Grid>
      </div>
      {/* <Demo2></Demo2> */}
    </div>
  );
}

export default Footer;
