import React from "react";
import { Grid } from "@mui/material";
import "./Resume.css";

function Resume() {
  return (
    <div id="resume">
      <div className="resume_content">
        <Grid
          container
          spacing={12}
          direction="row"
          justifyContent="center"
          sx={{
            padding: "30px 30px 150px 30px",
          }}
        >
          <Grid item>
            <h3 className="resume_content_header">Feature Work</h3>
          </Grid>
          <Grid item>
            <Grid
              container
              direction="column"
              spacing={6}
              sx={{ paddingLeft: "80px" }}
            >
              <Grid item>
                <Grid container spacing={12} rowSpacing={2}>
                  <Grid item>
                    <div className="resume_data_header">
                      <h3 style={{ minWidth: "10rem" }}>Trideviyaan</h3>
                      <p>2018</p>
                    </div>
                  </Grid>
                  <Grid item>
                    <p className="resume_data_text">
                      <p>Full House Entertainment. Sony Sabtv.</p>
                      Did Prominent and Continuity Role Name - Sunder, Brother
                      of Veteran Actor Ranjeet Ji for 8 months.
                    </p>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item>
                <Grid container spacing={12} rowSpacing={2}>
                  <Grid item>
                    <div className="resume_data_header">
                      <h3 style={{ minWidth: "10rem" }}>Manmohini</h3>
                      <p>2019</p>
                    </div>
                  </Grid>
                  <Grid item>
                    <p className="resume_data_text">
                      I did a Prominent and Continuity role as a GIRIJA SHANKAR
                      for around 8 months. In serial, I played many shades like
                      comedy, negative, serious, Settled, etc.
                    </p>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item>
                <Grid container spacing={12} rowSpacing={2}>
                  <Grid item>
                    <div className="resume_data_header">
                      <h3 style={{ minWidth: "10rem" }}>Mithai </h3>
                      <p>2022</p>
                    </div>
                  </Grid>
                  <Grid item>
                    <p className="resume_data_text">
                      I am happy to be back with the Zee TV family with Serial
                      "MITHAI" currently on-aired. In MITHAI i am playing an
                      prominent role of Animesh Mishra. Mine will be a positive
                      character.This family of mine will play a huge part in the
                      narrative.”
                    </p>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </div>
    </div>
  );
}

export default Resume;
