import { Grid, Link } from "@mui/material";
import React, { useState } from "react";
import "./Info.css";
import infoImage from "../assets/info_image.png";

const infoContent = (
  <div>
    <p>
      I am happy to be back with the Zee TV family with Serial "MITHAI"
      currently on - aired. I was last seen in ZeeTV Show MANMOHINI. In MITHAI i
      am playing an integral part and prominent role of Animesh Mishra. My son
      will be married to the lead boy's sister.
    </p>
    <p>
      I am making a comeback to TV after my stint with Manmohini. This is my
      first work after the Covid breakout. I am grateful to the channel for
      giving me this opportunity. Producer/ Director Arvind Babbal Sir and
      Casting Director Darpan Srivastavji have given me a very delightful role,
      and I look forward to this stint. Mine will be a positive character. One
      of my sons is married to the main lead's sister. My other son is on the
      verge of getting married to the other daughter from the same house.So this
      family of mine will play a huge part in the narrative.
    </p>
    RECENTLY I have done a serial MANMOHINI (Zeetv). I did a Prominent and
    Continuity role as a GIRIJA SHANKAR for around 8 months. I played many
    shades of comedy and did a Prominent and Continuity role as a GIRIJA SHANKAR
    for around 8 months. In serial, I played many shades like comedy, negative,
    serious, Settled, etc.{" "}
    <p>You can consider me in any kind of Role. Thanks.🙂</p>
  </div>
);

function Info() {
  const [readMore, setReadMore] = useState(false);

  return (
    <div id="info">
      {/* <Navbar></Navbar> */}
      <Grid
        container
        spacing={4}
        direction="row"
        justifyContent="center"
        alignItems="center"
        sx={{ padding: "100px 0" }}
      >
        <Grid
          item
          container
          position="relative"
          md={6}
          sm={12}
          justifyContent="center"
          alignItems="center"
        >
          <Grid item>
            <div className="info_content_image_backgound">
              <img src={infoImage} alt="" />
            </div>
          </Grid>
        </Grid>
        <Grid item md={6} sm={6} xs={8} container direction="column">
          <Grid item>
            <p className="info_text white">
              {readMore ? (
                <span>
                  <p>
                    I am happy to be back with the Zee TV family with Serial
                    "MITHAI" currently on - aired. I was last seen in ZeeTV Show
                    MANMOHINI. In MITHAI i am playing an integral part and
                    prominent role of Animesh Mishra. My son will be married to
                    the lead boy's sister.
                  </p>
                  <p>
                    I am making a comeback to TV after my stint with Manmohini.
                    This is my first work after the Covid breakout. I am
                    grateful to the channel for giving me this opportunity.
                    Producer/ Director Arvind Babbal Sir and Casting Director
                    Darpan Srivastavji have given me a very delightful role, and
                    I look forward to this stint. Mine will be a positive
                    character. One of my sons is married to the main lead's
                    sister. My other son is on the verge of getting married to
                    the other daughter from the same house.So this family of
                    mine will play a huge part in the narrative.
                  </p>
                  <p>
                    RECENTLY I have done a serial MANMOHINI (Zeetv). I did a
                    Prominent and Continuity role as a GIRIJA SHANKAR for around
                    8 months. I played many shades of comedy and did a Prominent
                    and Continuity role as a GIRIJA SHANKAR for around 8 months.
                    In serial, I played many shades like comedy, negative,
                    serious, Settled, etc.{" "}
                  </p>
                  <p>You can consider me in any kind of Role. Thanks.🙂</p>
                </span>
              ) : (
                <span>
                  <p>
                    I am happy to be back with the Zee TV family with Serial
                    "MITHAI" currently on - aired. I was last seen in ZeeTV Show
                    MANMOHINI. In MITHAI i am playing an integral part and
                    prominent role of Animesh Mishra. My son will be married to
                    the lead boy's sister.
                  </p>
                  <p>I am making a comeback to TV after my stint...</p>
                </span>
              )}
            </p>
          </Grid>
          <Grid item>
            <Link onClick={() => setReadMore(true)} underline="none">
              <p className="info_readMore">Read More</p>
            </Link>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}

export default Info;
